@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
* {
    font-family: 'Source Sans Pro', sans-serif;
}
*, *:before, *:after {
    box-sizing: border-box;
}

nav {
    margin-top: 1.5rem;
}

button {
    padding:.3rem;
    border:none;
    background-color: #8eacbc;
    color:#fff;
    font-weight: bold;
    border-radius:.3rem;
    cursor: pointer;
}
button:hover {
    background-color: #7d8a8f;
    border: 1px solif plum;
}

.navButton {
    text-decoration: none;
    padding:.3rem;
    border:none;
    background-color: #8eacbc;
    color:#fff;
    font-weight: bold;
    border-radius:.3rem;
    margin: .4rem;
}
.navButton:hover {
    border: 1px solif plum;
}

.panel {
    color: gray;
    position: fixed;
    left: 10;
    bottom: 0;
}

.centered {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: x-large;
  }

.grid {
    display: flex;
    flex-wrap: wrap;
    grid-template-columns: 50% 50%;
    justify-items: center;
    grid-auto-flow: row;
    /*grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));*/
    grid-gap: 1rem;
}
.grid-item {
    padding: 2rem;
}

.input {
    -webkit-animation: none 0s ease 0s 1 normal none running;
            animation: none 0s ease 0s 1 normal none running;
    -webkit-backface-visibility: visible;
            backface-visibility: visible;
    background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
    border: medium none currentColor;
    border-collapse: separate;
    border-image: none;
    border-radius: 0;
    border-spacing: 0;
    bottom: auto;
    box-shadow: none;
    box-sizing: content-box;
    caption-side: top;
    clear: none;
    clip: auto;
    color: #000;
    -webkit-columns: auto;
            columns: auto;
    -webkit-column-count: auto;
            column-count: auto;
    -webkit-column-fill: balance;
            column-fill: balance;
    grid-column-gap: normal;
    -webkit-column-gap: normal;
            column-gap: normal;
    -webkit-column-rule: medium none currentColor;
            column-rule: medium none currentColor;
    -webkit-column-span: 1;
            column-span: 1;
    -webkit-column-width: auto;
            column-width: auto;
    content: normal;
    counter-increment: none;
    counter-reset: none;
    cursor: auto;
    direction: ltr;
    display: inline;
    empty-cells: show;
    float: none;
    font-family: serif;
    font-size: medium;
    font-style: normal;
    font-feature-settings: normal;
    font-variant: normal;
    font-weight: normal;
    font-stretch: normal;
    line-height: normal;
    height: auto;
    -webkit-hyphens: none;
        -ms-hyphens: none;
            hyphens: none;
    left: auto;
    letter-spacing: normal;
    list-style: disc outside none;
    margin: 0;
    max-height: none;
    max-width: none;
    min-height: 0;
    min-width: 0;
    opacity: 1;
    orphans: 2;
    outline: medium none invert;
    overflow: visible;
    overflow-x: visible;
    overflow-y: visible;
    padding: 0;
    page-break-after: auto;
    page-break-before: auto;
    page-break-inside: auto;
    perspective: none;
    perspective-origin: 50% 50%;
    position: static;
    right: auto;
    -moz-tab-size: 8;
         tab-size: 8;
    table-layout: auto;
    text-align: left;
    text-align-last: auto;
    text-decoration: none;
    text-indent: 0;
    text-shadow: none;
    text-transform: none;
    top: auto;
    transform: none;
    transform-origin: 50% 50% 0;
    transform-style: flat;
    transition: none 0s ease 0s;
    unicode-bidi: normal;
    vertical-align: baseline;
    visibility: visible;
    white-space: normal;
    widows: 2;
    width: auto;
    word-spacing: normal;
    z-index: auto;
    all: initial;
    font-family: inherit;
    padding: .4rem;
    border-bottom: 2px solid lightgray;
    margin: 10px 0 10px;
    width: 100%;
}
.input:hover, input:focus {
    border-bottom: 2px solid plum;
}
table {
    border-collapse: collapse;
}
table > tbody > tr > th {
    border-bottom: 2px solid lightgray;
}
table > tbody > tr > th:nth-child(1) {
    border-bottom: 2px solid lightgray;
    border-right: 1px solid lightgray;
}
table > tbody > tr > td, th {
    padding: .6rem
}
table > tbody > tr > td:nth-child(1) {
    border-right: 1px solid lightgray;
}

.ankreuzen {
    padding-left: 2rem;
    padding-right: 2rem;
    text-align: center;
}

.trash {
    color:  #940d55;
    background-color: white;
    border: solid 1px #940d55;
}

.center {
    position: fixed;
    top: 20%;
    left: 20%;
}

.submitbutton {
    position: fixed;
    bottom: 0;
    width: 90%;
    margin: 2.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: x-large;
}
